@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600&display=swap');

body, .App {
    font-family: 'Quicksand', sans-serif;
    height: 1000px;
    width: 1800px;
}

@media (min-width: 1750px) {
    body, .App {
        width: 100vw;
        height: 100vh;
    }
    .man {
        left: 18vw;
    }
    .girl {
        left: 36vw;
    }
    
}
@media (min-width: 1100px) {

    .man {
        left: 17vw;
    }
    .girl {
        left: 36vw;
    }
}

.Home1 {
    height: 85vh;
}
.Home1 h1 {
    line-height: 100px;
    color: #430711;

}
.Banner span {
    position: relative;
}
.Banner span::after {
    content: '';
    background-color: #FFAB00;
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    border-radius: 5px;
    width: 0;
    height: 15%;
}

.active {
    border-bottom: 8px solid #FFAB00;
}

.Banner span:hover::after {
    transition: 0.5s;
    width: 60%;
}

.man {
    left: 380px;
}
.girl {
    left: 682px;
}
.Banner {
    width: 100%;
    display: flex;
    height: 120px;
    justify-content: space-evenly;
    align-items: center;
    font-size: 24px;
}

